        color: #9A9A9A;
        letter-spacing: -0.04px;
        text-align: right;
        line-height: 21px;
        padding-left: 3px;
    }

    .price {
        /*width: 100%;*/
        padding: 10px 15px;
    }

    .priceTag {
        font-size: 18px;
        color: #F43D3D;
        letter-spacing: -0.04px;
    }
    .goodsAttribute{
        display: flex;
        flex-wrap: wrap;
        color: #FB5768;
        font-size: 12px;
        margin-top:6px;
        .goodsAttributeBox{
            border: 0.5px solid #FB5768;
            border-radius: 10px;
            padding: 2px 7px;
            margin-right: 5px;
            margin-bottom: 5px;
            max-width: 89%;
            overflow: hidden;
            white-space: nowrap;
            text-overflow: ellipsis;
        }

    }
    .goodscoupon{
        display: flex;
        flex-wrap: wrap;
        color: #FB5768;
        font-size: 12px;
        margin-top:6px;
        .couponTitleBox{
            border: 0.5px solid rgba(251,87,104,0.25);
            // :style="{'border':'0.5px solid #FB5768'}"
            background:  rgba(251,87,104,0.25);
            border-radius: 10px;
            padding: 2px 7px;
            margin-right: 5px;
            margin-bottom: 5px;
            max-width: 89%;
            overflow: hidden;
            white-space: nowrap;
            text-overflow: ellipsis;
            // color: #FB5768;
            // font-size: 12px;
        }
    }


    .spuName {
        // width: 303px;
        width: 100%;
        font-family: PingFangSC-Medium;
        font-size: 17px;
        color: #333333;
        letter-spacing: -0.04px;
