    .shareImg {
        width: 18px;
        height: 18px;
        display: block;
        padding-left: 15px;
        padding-top: 8px;
        padding-right: 1.5px;
    }

    .goods {
        padding: 1px 15px;
        margin-bottom: 5px;
        .goodsDetailBox{
            display: flex;
            justify-content: space-between;
            width: 100%;
        }
    }

    .tag {
        font-family: PingFangSC-Regular;
        font-size: 10px;
        width: 92%;
        color: #333333;
        letter-spacing: -0.04px;
        padding: 5px 15px;
        background: #F9F9F9;
        line-height: 28px;
        display: flex;
        display: -webkit-flex;
        /* align-items: center; */
        flex-wrap: wrap;
        flex-direction:column;
    }

    .oneTag {
        display: flex;
        margin-right: 30px;
    }

    .redPoint {
        width: 4px;
        height: 4px;
        background-color: #ff5050;
        border-radius: 50% !important;
        margin-right: 5px;
        margin-top: 12px;
    }

    .tagInfo {
        vertical-align: middle;
        height: 100%;
        max-width: 340px;
        white-space: pre-line;
        font-size: 14px;
    }

    .spec {
        padding: 5px 15px;
        // border: 0 solid #E5E5E5;
        height: 38px;
        line-height: 38px;
    }

    .specTit {
        font-family: PingFangSC-Regular;
        font-size: 15px;
        color: #9A9A9A;
        letter-spacing: -0.04px;
        margin-right: 15px;
        float: left;
    }

    .couponTitBox {
        padding: 5px 15px;
        align-items: center;
        height: 38px;
        display: flex;
        justify-content: space-between;
    }

    .couponTit {
        height: 38px;
        line-height: 38px;
        margin-top: 0px;
        font-family: PingFangSC-Regular;
        font-size: 15px;
        color: #9A9A9A;
        letter-spacing: -0.04px;
        margin-right: 15px;
        float: left;
    }
    .goodscouponMore{
        width: 7px;
        height: 11px;
        margin-left: 5px;
    }

    .specName {
        font-family: PingFangSC-Medium;
        font-size: 15px;
        color: #333333;
        letter-spacing: -0.04px;
        float: left;
    }

    .borderLine {
        border: 0.5px solid #E5E5E5;
        width: 345px;
        margin-left: 15px;
    }
    .borderBox{
        width: 100%;
        height: 10px;
        background: #F9F9F9;
    }

    .showMore {
        width: 7px;
        height: 11px;
        vertical-align: middle;
        float: right;
        margin-top: 15px;
    }

    .shoppingNum {
        margin: 5px 15px;
        height: 50px;
        display: flex;
        justify-content: space-between;
        font-family: PingFangSC-Medium;
        font-size: 14px;
        color: #333333;
        letter-spacing: -0.04px;
        line-height: 50px;
    }
    .shoppingNumTitle{
        font-family: PingFangSC-Medium;
        font-size: 15px;
        color: #333333;
